<script>
import axios from 'axios';
export default {
  data() {
    return {
      page: 'sub',
      categories: ['новости', 'Блоги', 'Юмор', 'Технологии', 'Экономика', 'Бизнес', 'Маркетинг', 'Путешествия', 'Психология', 'Политика', 'Образование', 'Здоровье и фитнес', 'Право', 'Спорт', 'Медицина', 'Игры', 'Еда и кулинария', 'Дизайн', 'Криптовалюты', 'Другое'],
      categoryStates: {},
      nestedCategoryStates: {},
      name:'',
      data:[],
      tg:null,
      data1:[],
      id:'',
    }
  },
  methods: {
    Delete(channel_id){
      const apiUrl = 'https://myexampleapp.ru/State/delete-channel';
      const params = {
        userName: this.name,
        id: channel_id,
      };

      axios.get(apiUrl, {
        params,
        mode: 'cors'
      })
      .then(response => {
       this.request();
      })
      .catch(error => {
        console.error(`Error fetching channels: ${error}`);
      });
    },
    request(){
      const apiUrl = 'https://myexampleapp.ru/State/get-channels-id';
      const params = {
        userName: this.name,
      };

      axios.get(apiUrl, {
        params,
        mode: 'cors'
      })
      .then(response => {
        const responseData = response.data;

        if (responseData != undefined){
          this.data = responseData;
          this.request2();
        }
      })
      .catch(error => {
        console.error(`Error fetching channels: ${error}`);
      });
    },
    request2() {
     
    const apiUrl = 'https://myexampleapp.ru/State/get-channel-info';

    this.data.forEach(channel => {
      const params = {
        channel: channel, 
      };

      axios.get(apiUrl, {
        params,
        mode: 'cors'
      })
      .then(response => {

        const responseData = response.data;

        if (responseData != undefined){

          responseData.icon = responseData.icon ? `data:image/png;base64,${responseData.icon}` : ''; 
          this.data1.push(responseData);

        }
      })
      .catch(error => {
        console.error(`Error fetching channels: ${error}`);
      });
    });
  },
    change(name) {
      this.page = name;
    },
    showMenu(category) {
      this.categoryStates[category] = !this.categoryStates[category];
      
    },
    showNestedMenu(category) {
      this.nestedCategoryStates[category] = !this.nestedCategoryStates[category];
    }
  },
  mounted() {
    const telegramApi = window.Telegram.WebApp;
    this.tg = telegramApi;
    telegramApi.ready();
    try {
      this.name = telegramApi.initDataUnsafe.user.username;
      
    } catch (error) {
      console.log(error)
    }
    this.request();
   

  }
}
</script>


<template>
  <div class="tg-container">
    <div class="container">
      <div class="tabs">
        <button class="tab" :class="{ active: page === 'sub' }" data-tab="subscriptions" @click="change('sub')">Подписки</button>
        <button class="tab" :class="{ active: page === 'rec' }" data-tab="recommendations" @click="change('rec')">Рекомендации</button>
      </div>
      <div class="tab-content" v-if="page === 'sub'">
        <div id="duck" v-if="data[0] == undefined">
          <h2>Вы пока не подписались ни на один канал</h2>
          <img src="./assets/duck.gif" class="duck">
          <p>выберите темы и мы поможем с каналами</p>
        </div>
        <div id="notduck" v-else>
        
          <div class="channel-card" v-for="(item, index) in data1" :key="index">
            <img class="channel-image" :src="item.icon" v-if="item.icon" />
            <div class="channel-info">
              <h3 class="channel-title">{{ item.title }}</h3>
              <p class="channel-description">{{ item.description }}</p>
              <p class="channel-sub">Подписчиков:{{ item.subscribers }}</p>
            </div>
            <button class="dropdown-button1" @click="Delete(item.idChannel)">-</button>
          </div>
        </div>
        <div class="cont">
          <div class="dropdown" v-for="(category, index) in categories" :key="index">
            <button class="dropdown-button" @click="showMenu(category)">{{ category }}</button>
            <ul class="dropdown-menu" v-if="categoryStates[category]">
              <div class="channel-card" v-if="1!=1">
                <img class="channel-image" />
                <div class="channel-info">
                  <h3 class="channel-title">channel</h3>
                  <p class="channel-description">channel</p>
                  <p class="channel-sub">channel</p>
                </div>
              </div>
              <h3 v-else>Не найдено</h3>
            </ul>
          </div>
        </div>
      </div>
      <div class="tab-content" v-else>
        <div class="cont">
          <div class="dropdown" v-for="(category, index) in categories" :key="index">
            <button class="dropdown-button" @click="showMenu(category)">{{ category }}</button>
            <ul class="dropdown-menu" v-if="categoryStates[category]">
              <div class="channel-card" v-if="1!=1">
                <img class="channel-image" />
                <div class="channel-info">
                  <h3 class="channel-title">channel</h3>
                  <p class="channel-description">channel</p>
                  <p class="channel-sub">channel</p>
                </div>
              </div>
              <h3 v-else>Не найдено</h3>            
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>



